import React, { Component } from 'react'
import cs from 'classnames'

import Mask from 'components/Mask/'
import 'stylesheets/factory'
import Factory from './lib/Factory'

export default class component extends Component {
	constructor(props) {
		super(props)
		this.state = {
			gameProgress: 100,
			gameSign: false,
			helpShow: false,
			word: 'Game Start',
			game: null
		}
		this.switchState = this.switchState.bind(this)
		this.toggleHelp = this.toggleHelp.bind(this)
		this.clickFn = this.clickFn.bind(this)
	}
	componentDidMount() {
		// this.switchState()
	}
	switchState() {
		let obj = {
			gameSign: !this.state.gameSign
		}
		if (!this.state.game) {
			obj.game = new Factory(this.refs.factory)
		}
		else {
			obj.word = 'Continue'
		}
		this.setState(obj)
	}
	toggleHelp() {
		this.setState({
			helpShow: !this.state.helpShow
		})
	}
	clickFn() {
		this.state.game.start()
	}
	render() {
		let GameClass = cs('game-main', {
			show: this.state.gameSign
		})
		let HelpClass = cs('game-help', {
			show: this.state.helpShow
		})
		let GroupClass = cs('game-prev-group', {
			hide: this.state.gameSign
		})
		return (
			<div ref="view" className="factory-view">
				<Mask show={this.state.helpShow} onClick={this.toggleHelp} zIndex={8} />
				<div className={GroupClass}>
					<section className="title-group">
						<h1 className="game-title">
							Factory
						</h1>
						<small className="version">beta</small>
					</section>
					<button className="game-btn" onClick={this.switchState}>{ this.state.word }</button>
					<button className="game-btn" onClick={this.toggleHelp}>Help</button>
				</div>
				<div className={GameClass}>
					<div className="game-btn-group">
						<button onClick={this.switchState}>
							<i className="iconfont icon-left"></i>
						</button>
						<button onClick={this.toggleHelp}>Help</button>
					</div>
					<div ref="factory" className="factory"></div>
				</div>
				<div className={HelpClass}>
					<i className="iconfont icon-close" onClick={this.toggleHelp}></i>
					<section>
						<h3></h3>
						<p className="help-item">
							(OUT)(EXP)
						</p>
					</section>

					<section>
						<h3></h3>
						<p className="help-item">
							<span>NEXT</span>
							INPUT(Processor), ACC
						</p>
						<p className="help-item">
							<span>ACC</span>
							(INPUT), (OUTPUT)
						</p>
						<p className="help-item">
							<span>COM</span>
							(Processor), C
						</p>
					</section>

					<section>
					<h3></h3>
						<p className="help-item">
							<span>T</span>
							ACC, (Processor), OUTPUT
						</p>
						<p className="help-item">
							<span>B</span>
							ACC, (Processor), OUTPUT
						</p>
						<p className="help-item">
							<span>L</span>
							ACC, (Processor), OUTPUT
						</p>
						<p className="help-item">
							<span>R</span>
							ACC, (Processor), OUTPUT
						</p>
						<p className="help-item">
							<span>C</span>
							(COM), (ACC)
						</p>
					</section>

					<section>
					<h3></h3>
						<p className="help-item">
							ES6()
						</p>
						<p className="help-item">
							, windowWebStorage
						</p>
					</section>

					<section>
						<h3></h3>
						<p className="help-item">
							, BUG<a href="https://nightcatsama.github.io/NightCat/dist/" target="_blank">ISSUE</a>
						</p>
						<p className="help-item">
							, GITHUB<a href="https://github.com/NightCatSama/NightCat" target="_blank">https://github.com/NightCatSama/NightCat</a>
						</p>
					</section>
				</div>
			</div>
		)
	}
}

component.propTypes = {}


